import React, {useEffect} from 'react'
import styled from 'styled-components'
import '../assets/css/0_basic.scss'
import AOS from 'aos';
import "aos/dist/aos.css";  
import Personal_Colors from '../assets/img/exhibit_main/Personal_Colors.png'
// import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import media from '../media'

    const TextPng = styled.img`
        width: 596px;
        height: 108px;
        align-self: flex-start;
        ${media.mobile`
            width: 200px;
            height: 36px;
        `}
    `

    const Media = styled.video`
        width: 976px;
        height: 512px;
        background-color: rgba(222, 236, 239, 1);
        margin-top: 24px;
        ${media.mobile`
            width: 328px;
            height: 184px;
        `}        
    `

    const Mtitle = styled.div`
        font-size: 28px;
        margin-top: 24px;
        letter-spacing: -0.03em;
        color: #5E5E5E;
        ${media.mobile`
            font-size: 12px;
            margin-top: 16px;
        `}        
    `;

    const Text = styled.div`
        font-size: 22px;
        line-height: 20px;
        margin-top: 128px;
        text-align: right;
        color: #7A8082;
        opacity: 0.7;
        letter-spacing: -0.03em;
        ${media.mobile`
            font-size: 8px;
            margin-top: 40px;
            line-height: 8px;
        `}        
    `;
    

function Content1() {

    useEffect(() => {
        AOS.init();
    }, []);  

    return (
        <>
            <TextPng src={Personal_Colors}></TextPng>
            <Media></Media>
            <Mtitle>    ?</Mtitle>
            <Text>   ,      .
            <br/><br/>'  ,     '
            <br/><br/>',          '
            <br/><br/>'  ?      ?''</Text>
        </>
    )
}

export default Content1;