import React, {useEffect} from 'react'
import styled from 'styled-components'
import '../assets/css/0_basic.scss'
import AOS from 'aos';
import "aos/dist/aos.css";
import media from '../media'

    const Btitle = styled.div`
        font-family: 'Eulyoo1945';
        font-weight: 400;
        font-size: 32px;
        line-height: 32px;
        letter-spacing: -0.02em;
        color: #5E5E5E;
        ${media.mobile`
            font-size: 12px;
            line-height: 12px;
        `}
    `;

    const Mtitle = styled.div`
        font-family: 'Eulyoo1945_semi' !important;
        font-weight: 600;
        font-size: 28px;
        line-height: 28px;
        text-align: ${(props)=>props.ta};
        color: ${(props)=>props.color};
        margin-top: ${(props)=>props.mt};
        margin-left: ${(props)=>props.ml};
        margin-right: ${(props)=>props.mr};
        ${media.mobile`
            font-size: 10px;
            line-height: 10px;
            margin: 0px;
            margin-top: ${(props)=>props.mmt};
        `}
    `;

    const Text = styled.div`
        font-size: 20px;
        line-height: 36px;
        line-height: ${(props)=>props.lh};
        font-family: ${(props)=>props.font};
        text-align: ${(props)=>props.ta};
        color: ${(props)=>props.color};
        margin-top: ${(props)=>props.mt};
        margin-left: ${(props)=>props.ml};
        margin-right: ${(props)=>props.mr};
        ${media.mobile`
            font-size: 8px;
            line-height: 14px;
            margin: 0px;
            margin-left: ${(props)=>props.mml};
            margin-top: ${(props)=>props.mmt};
        `}
    `;

    const Span = styled.span`
        font-family: 'Eulyoo1945_semi' !important;
        font-weight: 600;
    `

function Content2() {

    useEffect(() => {
        AOS.init();
    }, []);    

    return (
        <>
            {/* fade up, fade right, zoom out */}
            <Btitle>       .</Btitle>
            <Mtitle color="#7C95C3" mt="88px" ta="right" mr="16px" mmt="40px">  ?</Mtitle>
            <div>
                <Text color="#7C95C3" ta="right" mt="120px" mr="16px" mmt="40px">     .'
                <br/>   . '
                <br/><br/>  ,    .
                <br/>    .
                <br/>    .
                <br/><br/> ,  ,      .
                <br/> ,       .</Text>
            </div>
            {/* fade left, zoom out */}
            <Mtitle color="#5E5E5E" mt="104px" ml="16px" ta="left" mmt="40px">?  , ?</Mtitle>
            <div>
                <Text lh="32px" color="#5E5E5E" mt="48px" ml="48px" mmt="24px" mml="12px">
                    ,      .
                <br/>  ,  '   ' 
                <br/>    .</Text>
                <Text color="#5E5E5E" mt="80px" ml="48px" font="'Eulyoo1945_semi' !important" fw="600" mmt="24px" mml="12px">
                    "    ?"
                    <br/>"    ?"</Text>
            </div>
            {/* fade up */}
            <Mtitle color="#7C95C3" mt="120px" ta="center" mmt="30px">   ?</Mtitle>
            <div>
                <Text color="#7C95C3" mt="72px" ta="center" mmt="32px">
                        ( ) , <Span>  </Span>.
                <br/><br/>  ,    ,
                <br/>         .
                <br/><br/> , <Span>  Collaboration</Span>      .
                <br/><br/>  Main Collaboration Artist_ Yuki  ,
                <br/> Personal Color     .
                <br/><br/> <Span>     </Span>,
                <br/> <Span>   ,  Color </Span> .</Text>
            </div>
        </>
    )
}

export default Content2;